import * as React from 'react';

import {
  CardSet,
  Card,
  mergeCards,
  getCardSets,
  removeCardSet,
  removeCard,
  addCards,
  createCardSet,
} from '../db';

export interface AppContextValue {
  cardSets: CardSet[];
  onMergeCards: (cardSetId: string, cards: Card[]) => void;
  onDeleteCardSet: (cardSetId: string) => void;
  onDeleteCard: (cardSetId: string, cardId: string) => void;
  onAddCards: (cardSetId: string, cards: Card[]) => void;
  onCreateCardSet: () => void;
}

const noop = () => {};

export const AppContext = React.createContext<AppContextValue>({
  cardSets: [],
  onMergeCards: noop,
  onDeleteCardSet: noop,
  onDeleteCard: noop,
  onAddCards: noop,
  onCreateCardSet: noop,
});

export interface AppContextContainerProps {
  children: JSX.Element;
  [key: string]: any;
}

export const AppContextContainer = (props: AppContextContainerProps) => {
  const [ cardSets, setCardSets ] = React.useState<CardSet[]>([]);

  const handleMergeCards = React.useCallback(async (cardSetId: string, cards: Card[]) => {
    await mergeCards(cardSetId, cards);
    const cardSetsUpdated = await getCardSets();
    setCardSets(cardSetsUpdated);
  }, [ setCardSets ]);

  const handleDeleteCardSet = React.useCallback(async (cardSetId: string) => {
    await removeCardSet(cardSetId);
    const cardSetsUpdated = await getCardSets();
    setCardSets(cardSetsUpdated);
  }, [ setCardSets ]);

  const handleDeleteCard = React.useCallback(async (cardSetId: string, cardId: string) => {
    await removeCard(cardSetId, cardId);
    const cardSetsUpdated = await getCardSets();
    setCardSets(cardSetsUpdated);
  }, [ setCardSets ]);

  const handleAddCards = React.useCallback(async (cardSetId: string, cards: Card[]) => {
    await addCards(cardSetId, cards);
    const cardSetsUpdated = await getCardSets();
    setCardSets(cardSetsUpdated);
  }, [ setCardSets ]);

  const handleCreateCardSet = React.useCallback(async () => {
    await createCardSet();
    const cardSetsUpdated = await getCardSets();
    setCardSets(cardSetsUpdated);
  }, [ setCardSets ]);

  React.useEffect(() => {
    (async () => {
      const sets = await getCardSets();
      setCardSets(sets);
    })();
  }, []);

  const contextValue: AppContextValue = {
    cardSets,
    onMergeCards: handleMergeCards,
    onDeleteCardSet: handleDeleteCardSet,
    onDeleteCard: handleDeleteCard,
    onAddCards: handleAddCards,
    onCreateCardSet: handleCreateCardSet,
  };

  return (
    <AppContext.Provider value={ contextValue }>
      { props.children }
    </AppContext.Provider>
  );
};
