import * as React from 'react';
import { Button, Card as BPCard, Tag, Colors } from '@blueprintjs/core';
import { format } from 'date-fns';
import { css, StyleSheet } from 'aphrodite';

const styles = StyleSheet.create({
  content: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  info: {
    color: Colors.GRAY2,
    display: 'flex',
    flexDirection: 'column',
    // alignItems: 'center',
  },
});

export interface CardSetThumbnailProps {
  /** Timestamp. */
  created: number;
  onDelete: (id: string) => void;
  cardsetId: string;
  onClick?: (id: string) => void;
  className?: string;
  /** How many cards in card set. */
  size: number;
}

export const CardSetThumbnail = (props: CardSetThumbnailProps) => {
  const {
    created,
    onDelete,
    cardsetId,
    onClick,
    className,
    size,
  } = props;

  const handleDelete = React.useCallback((e: React.SyntheticEvent) => {
    e.stopPropagation();
    onDelete(cardsetId);
  }, [ onDelete, cardsetId ]);

  const handleClick = React.useCallback(() => {
    if (onClick) {
      onClick(cardsetId);
    }
  }, [ onClick, cardsetId ]);

  return (
    <BPCard onClick={ handleClick } className={ className } interactive>
      <div className={ css(styles.content) }>
        <div className={ css(styles.info) }>
          <span>created at { format(created, 'yyyy-MM-dd HH:mm') }</span>
          <span>cards in set { size }</span>
        </div>
        <Button intent='danger' onClick={ handleDelete }>delete</Button>
      </div>
    </BPCard>
  );
};
