import * as React from 'react';
import { useHistory, useParams } from 'react-router-dom';

import { AppContext } from '../context/AppContext';
import { getExerciseLink } from '../routes';

import { CardListView } from '../views/CardListView';
import { AddCardsDialog } from './AddCardsDialog';

export const CardList = () => {
  const history = useHistory();
  const cardsetId = useParams().cardsetId;
  const ctx = React.useContext(AppContext);
  if (DEBUG) {
    if (!ctx) {
      throw new Error('Probably there is no AppContext.Provider uper in component tree.');
    }
  }

  const cardSet = ctx.cardSets.find((cs) => cs.id === cardsetId);
  const cards = cardSet ? cardSet.cards : [];

  const handleDeleteCard = React.useCallback((cardId: string) => {
    ctx.onDeleteCard(cardsetId, cardId);
  }, [ ctx, cardsetId ]);

  const handleStartExercise = React.useCallback(() => {
    history.push(getExerciseLink(cardsetId));
  }, [ history, cardsetId ]);

  const [ displayAddCardsDialog, setDisplayCardsDialog ] = React.useState(false);

  const handleAddCards = React.useCallback(() => {
    setDisplayCardsDialog(true);
  }, [ setDisplayCardsDialog ]);

  const handleAddCardsDialogClose = React.useCallback(() => {
    setDisplayCardsDialog(false);
  }, [ setDisplayCardsDialog ]);

  const handleHomeClick = React.useCallback(() => {
    history.push('/');
  }, [ history ]);

  return (
    <>
      <AddCardsDialog open={ displayAddCardsDialog } onClose={ handleAddCardsDialogClose } />
      <CardListView
        onHomeClick={ handleHomeClick }
        cards={ cards }
        onStartExercise={ handleStartExercise }
        onCardDelete={ handleDeleteCard }
        onAddCards={ handleAddCards }
      />
    </>
  );
};
