import * as React from 'react';
import { useParams, useHistory } from 'react-router-dom';

import { Card } from '../db';
import { ExerciseView } from '../views/ExerciseView';
import { AppContext } from '../context/AppContext';
import { getCardListLink } from '../routes';

const SELECTION_SIZE = 15;

export const Exercise = () => {
  const history = useHistory();
  const cardsetId = useParams().cardsetId;
  const ctx = React.useContext(AppContext);
  if (DEBUG) {
    if (!ctx) {
      throw new Error('Probably there is no AppContext.Provider uper in component tree.');
    }
  }

  const handleExerciseEnd = React.useCallback((cards: Card[]) => {
    ctx.onMergeCards(cardsetId, cards);
    history.replace(getCardListLink(cardsetId));
  }, [ cardsetId, ctx ]);

  const cardSet = ctx.cardSets.find((cs) => cs.id === cardsetId);

  const selectedCards = cardSet ? selectCards(cardSet.cards, SELECTION_SIZE) : [];

  return (
    <ExerciseView onExerciseEnd={ handleExerciseEnd } cards={ selectedCards } />
  );
};

export const selectCards = (cards: Card[], selectionSize: number): Card[] => {
  const cardsCopy = [ ...cards ];
  return cardsCopy.sort(compare).slice(0, selectionSize);
};

const compare = (a: Card, b: Card): number => {
  const rateA = a.viewCount !== 0 ? a.successCount / a.viewCount : 0;
  const rateB = b.viewCount !== 0 ? b.successCount / b.viewCount : 0;
  const factA = rateA !== 0 ? rateA * a.lastView : a.lastView;
  const factB = rateB !== 0 ? rateB * b.lastView : b.lastView;
  // bigger factor means futher from array head the value placed
  return factA - factB;
};
