import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import { Button, Card as BPCard, Tag, Colors } from '@blueprintjs/core';
import { format } from 'date-fns';

import { Card } from '../db/CardSets';

const styles = StyleSheet.create({
  content: {
    display: 'flex',
    flexDirection: 'column',
  },
  info: {
    display: 'flex',
    justifyContent: 'space-between',
    margin: '5px 0',
  },
  deleteButton: {
    //
  },
  lastExercised: {
    color: Colors.GRAY2,
  },
});

export interface CardThumbnailProps extends Pick<Card, 'lastView' | 'successCount' | 'viewCount'> {
  title: string;
  cardId: string;
  onDelete: (id: string) => void;
  className?: string;
}

export const CardThumbnail = (props: CardThumbnailProps) => {
  const {
    cardId,
    title,
    lastView,
    successCount,
    viewCount,
    onDelete,
    className,
  } = props;

  const handleDelete = React.useCallback(() => {
    onDelete(cardId);
  }, [ onDelete, cardId ]);

  const successRate = viewCount > 0 ? successCount / viewCount : 0;
  let intent: 'success' | 'warning' | 'danger' = 'danger';
  if (successRate > 0.6) {
    intent = 'warning';
  }
  if (successRate > 0.9) {
    intent = 'success';
  }
  let lastExercised = 'never';
  if (lastView !== 0) {
    lastExercised = format(lastView, 'yyyy-MM-dd HH:mm');
  }

  return (
    <BPCard className={ className } >
      <h4>{ title }</h4>
      <div className={ css(styles.content) }>
        <div className={ css(styles.info) }>
          <Tag intent={ intent }>learned { Math.floor(successRate * 100) }%</Tag>
          <Button intent='danger' className={ css(styles.deleteButton) } onClick={ handleDelete }>delete</Button>
        </div>
        <div className={ css(styles.lastExercised) }>last exercised: { lastExercised }</div>
      </div>
    </BPCard>
  );
};
