import * as localforage from 'localforage';
import update from 'immutability-helper';

import { createUniqId, mergeArrays } from '../lib';

export const COLLECTION_NAME = DEV ? 'CARD_SETS_DEV' : TEST ? 'CARD_SETS_TEST' : 'CARD_SETS';

export interface Card {
  sideA: string;
  sideB: string;
  viewCount: number;
  successCount: number;
  /** Timestamp. */
  lastView: number;
}

export interface CardSet {
  id: string;
   /** Timestamp. */
  created: number;
  cards: Card[];
}

/** Create a new Card object. */
export const createCard = (params: Pick<Card, 'sideA' | 'sideB'>) => {
  const { sideA, sideB } = params;

  return {
    sideA,
    sideB,
    viewCount: 0,
    successCount: 0,
    lastView: 0,
  };
};

export const createCardSetObject = (): CardSet => {
  return {
    id: createUniqId(),
    created: new Date().getTime(),
    cards: [],
  };
};

/** Get all cardsets from storage. */
export const getCardSets = async (): Promise<CardSet[]> => {
  const sets = await localforage.getItem<CardSet[]>(COLLECTION_NAME);
  return sets || [];
};

/** Clear storage. */
export const removeAllCardSets = async () => {
  await localforage.setItem(COLLECTION_NAME, []);
};

/** Add new empty cardset to storage. */
export const createCardSet = async () => {
  const cardSets = await localforage.getItem<CardSet[]>(COLLECTION_NAME);
  await localforage.setItem(COLLECTION_NAME, [ ...(cardSets || []), createCardSetObject() ]);
};

/** Remove cardset by index from storage. */
export const removeCardSet = async (cardSetId: string) => {
  const sets = await localforage.getItem<CardSet[]>(COLLECTION_NAME);
  const index = sets.findIndex((cardSet) => cardSet.id === cardSetId);
  if (index !== -1) {
    await localforage.setItem(COLLECTION_NAME, update(sets, { $splice: [[index, 1]] }));
  }
};

/** Add cards to cardset by index. */
export const addCards = async (cardSetId: string, cards: Card[]) => {
  const sets = await localforage.getItem<CardSet[]>(COLLECTION_NAME);
  const index = sets.findIndex((cardSet) => cardSet.id === cardSetId);
  if (index !== -1) {
    await localforage.setItem(
      COLLECTION_NAME,
      update(sets, { [index]: { cards: { $push: cards } } }),
    );
  }
};

/** Merge cards into cardset by index ( properties of already existing cards replaced with new ones ). */
export const mergeCards = async (cardSetId: string, cards: Card[]) => {
  const sets = await localforage.getItem<CardSet[]>(COLLECTION_NAME);
  const index = sets.findIndex((cardSet) => cardSet.id === cardSetId);
  if (index !== -1) {
    const cardsUpdated: Card[] = mergeArrays(sets[index].cards, cards, (a, b) => a.sideA === b.sideA);
    await localforage.setItem(
      COLLECTION_NAME,
      update(sets, { [index]: { cards: { $set: cardsUpdated } } }),
    );
  }
};

/** Find te cardset by index and delete card by card id ( sideA ) from the cardset. */
export const removeCard = async (cardSetId: string, cardId: string) => {
  const sets = await localforage.getItem<CardSet[]>(COLLECTION_NAME);
  const index = sets.findIndex((cardSet) => cardSet.id === cardSetId);
  if (index !== -1) {
    const cardsUpdated = sets[index].cards.filter((card) => card.sideA !== cardId);
    await localforage.setItem(
      COLLECTION_NAME,
      update(sets, { [index]: { cards: { $set: cardsUpdated } } }),
    );
  }
};
