import * as React from 'react';
import * as ReactDOM from 'react-dom';
import '@blueprintjs/icons/lib/css/blueprint-icons.css';
import '@blueprintjs/core/lib/css/blueprint.css';
import './normalize.css';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { css, StyleSheet } from 'aphrodite';

const styles = StyleSheet.create({
  container: {
    padding: '10px',
    width: '100%',
    height: '100vh',
  },
});

import {
  MainPage,
  CardListPage,
  ExercisePage,
} from './pages';
import { routes } from './routes';
import { AppContextContainer } from './context/AppContext';
import { ROUTER_BASE_NAME } from './settings';

const App = () => {
  return (
    <div className={ css(styles.container) }>
      <AppContextContainer>
        <Router basename={ ROUTER_BASE_NAME }>
          <Route exact path={ routes.main } component={ MainPage } />
          <Route exact path={ routes.cardList } component={ CardListPage } />
          <Route exact path={ routes.exercise } component={ ExercisePage } />
        </Router>
      </AppContextContainer>
    </div>
  );
};

ReactDOM.render(<App />, document.getElementById('root'));
