import * as React from 'react';
import { Dialog, Button, Text, Classes, Callout, FileInput, InputGroup, ControlGroup } from '@blueprintjs/core';
import classnames from 'classnames';
import { css, StyleSheet } from 'aphrodite';

import { arrayBufferToString } from '../lib';

const styles = StyleSheet.create({
  form: {
    margin: '15px 10px 0 10px',
  },
  input: {
    flex: '2 0 auto',
  },
  button: {
    flex: '1 0 auto',
  },
  error: {
    margin: '5px 0',
  },
});

export interface AddDataDialogProps {
  onFileLoaded: (data: ArrayBuffer) => void;
  onClose: () => void;
  open: boolean;
}

export const AddDataDialog = (props: AddDataDialogProps) => {
  const { onFileLoaded, onClose, open } = props;

  const [ selectedFile, setSelectedFile ] = React.useState<any>(null);
  const [ errors, setErrors ] = React.useState<string[]>([]);

  const handleSubmit = React.useCallback(async () => {
    if (selectedFile) {
      const data = await selectedFile.arrayBuffer();
      onFileLoaded(data);
      setSelectedFile(null);
    }
  }, [ selectedFile, onFileLoaded, setSelectedFile ]);

  const handleChange = React.useCallback(async (e: React.SyntheticEvent<HTMLInputElement>) => {
    if (DEBUG) {
      if (!e.currentTarget.files || !e.currentTarget.files[0]) {
        throw new Error(`e.currentTarget.files should be an array of one file but it is ${ e.currentTarget.files }`);
      }
    }
    if (e.currentTarget.files && e.currentTarget.files[0]) {
      const file = e.currentTarget.files[0] as any;
      const data = await file.arrayBuffer();
      const validation = validateFileInput(data);
      if (validation.valid) {
        setSelectedFile(file);
        setErrors([]);
      } else if (validation.errors && validation.errors.length > 0) {
        setErrors(validation.errors);
      } else {
        setErrors(['Invalid input']);
      }
    }
  }, [ setSelectedFile, setErrors ]);

  const errorMessages = errors && errors.map((err) => (
    <Callout intent='danger' key={ err } className={ css(styles.error) }>
      { err }
    </Callout>
  ));

  return (
    <Dialog isOpen={ open } title='Choose file ( in csv format )' onClose={ onClose }>
      <form className={ css(styles.form) }>
        { errorMessages }
        <ControlGroup fill>
          <FileInput onInputChange={ handleChange } text={ selectedFile ? selectedFile.name : 'Choose file...' } />
          <Button intent='primary' onClick={ handleSubmit }>submit</Button>
        </ControlGroup>
      </form>
    </Dialog>
  );
};

export interface ValidationResult {
  valid: boolean;
  errors?: string[];
}

export const validateFileInput = (data: ArrayBuffer): ValidationResult => {
  const str = arrayBufferToString(data);
  let newLineChar = '\n';
  if (str.indexOf('\r\n') !== -1) {
    newLineChar = '\r\n';
  }
  const lines = str.split(newLineChar);
  if (lines.length === 0) {
    return {
      valid: false,
      errors: ['File has unsupported format'],
    };
  }
  const words = lines[0].split(',');
  if (words.length !== 4) {
    return {
      valid: false,
      errors: ['File has unsupported format'],
    };
  }

  return { valid: true };
};
