import * as React from 'react';
import { useHistory } from 'react-router-dom';

import { AppContext } from '../context/AppContext';
import { getCardListLink } from '../routes';
import { CardSetListView } from '../views/CardSetListView';

export const CardSetList = () => {
  const history = useHistory();
  const ctx = React.useContext(AppContext);
  if (DEBUG) {
    if (!ctx) {
      throw new Error('Probably there is no AppContext.Provider uper in component tree.');
    }
  }

  const handleCreateCardSet = React.useCallback(() => {
    ctx.onCreateCardSet();
  }, [ ctx ]);

  const handleDeleteCardSet = React.useCallback((cardsetId: string) => {
    ctx.onDeleteCardSet(cardsetId);
  }, [ ctx ]);

  const handleCardSetClick = React.useCallback((cardsetId: string) => {
    history.push(getCardListLink(cardsetId));
  }, [ history ]);

  const handleHomeClick = React.useCallback(() => {
    history.push('/');
  }, [ history ]);

  return (
    <>
      <CardSetListView
        onCardSetDelete={ handleDeleteCardSet }
        onCreateCardSet={ handleCreateCardSet }
        cardSets={ ctx.cardSets }
        onCardSetClick={ handleCardSetClick }
        onHomeClick={ handleHomeClick }
      />
    </>
  );
};
