import * as React from 'react';
import { Button } from '@blueprintjs/core';
import { css, StyleSheet } from 'aphrodite';

import { CardSetThumbnail } from './CardSetThumbnail';
import { CardSet } from '../db';
import { List } from './List';
import { Navbar } from './Navbar';

const styles = StyleSheet.create({
  listItem: {
    margin: '10px 0',
  },
});

export interface CardSetListViewProps {
  onCreateCardSet: () => void;
  onCardSetDelete: (cardSetId: string) => void;
  cardSets: CardSet[];
  onCardSetClick?: (cardsetId: string) => void;
  onHomeClick?: () => void;
}

export const CardSetListView = (props: CardSetListViewProps) => {
  const {
    onCreateCardSet,
    onCardSetDelete,
    cardSets,
    onCardSetClick,
    onHomeClick,
  } = props;

  const cardSetElements = cardSets.map((cardSet) => (
    <CardSetThumbnail
      created={ cardSet.created }
      cardsetId={ cardSet.id }
      onDelete={ onCardSetDelete }
      key={ cardSet.created }
      onClick={ onCardSetClick }
      className={ css(styles.listItem) }
      size={ cardSet.cards.length }
    />
  ));

  const navbarRightItems = <Button intent='primary' onClick={ onCreateCardSet }>create card set</Button>;

  return (
    <>
      <Navbar onHomeClick={ onHomeClick } rightItems={ navbarRightItems } />
      <List>
        { ...cardSetElements }
      </List>
    </>
  );
};
