import * as React from 'react';
import update from 'immutability-helper';
import { css, StyleSheet } from 'aphrodite';
import { Button } from '@blueprintjs/core';

import { Card } from '../db/CardSets';
import { CardView } from './CardView';
import { SuccessDialog } from './SuccessDialog';

const styles = StyleSheet.create({
  buttons: {
    display: 'flex',
    margin: '10px',
    justifyContent: 'space-between',
  },
  button: {
    margin: '0 5px',
  },
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    height: '100%',
  },
});

export interface ExerciseViewProps {
  onExerciseEnd: (cards: Card[]) => void;
  cards: Card[];
}

export const ExerciseView = (props: ExerciseViewProps) => {
  const {
    onExerciseEnd,
    cards,
  } = props;

  const [ modifiedCards, setModifiedCards ] = React.useState<Card[]>([ ...cards ]);
  const [ currentIndex, setCurrentIndex ] = React.useState<number>(0);

  const handleResponse = useHandleResponse({
    cards: modifiedCards,
    setCards: setModifiedCards,
    currentIndex,
    setCurrentIndex,
  });

  const handleSuccessDialogClose = React.useCallback(() => {
    onExerciseEnd(modifiedCards);
  }, [ onExerciseEnd, modifiedCards ]);

  if (currentIndex < modifiedCards.length) {
    return (
      <div className={ css(styles.container) }>
        <CardView { ...modifiedCards[currentIndex] } key={ modifiedCards[currentIndex].sideA } />
        <div className={ css(styles.buttons) }>
          <Button
            fill
            className={ css(styles.button) }
            name='yes'
            intent='success'
            onClick={ handleResponse as any }
          >
            know
          </Button>
          <Button
            fill
            className={ css(styles.button) }
            name='no'
            intent='danger'
            onClick={ handleResponse as any }
          >
            dont know
          </Button>
        </div>
      </div>
    );
  }

  return <SuccessDialog message='Exercise completed.' onClose={ handleSuccessDialogClose } />;
};

export interface UseHandleResponseArgs {
  cards: Card[];
  setCards: (cards: Card[] | ((cards: Card[]) => Card[])) => void;
  setCurrentIndex: (i: number | ((i: number) => number)) => void;
  currentIndex: number;
}

export const useHandleResponse = (args: UseHandleResponseArgs) => {
  const {
    cards,
    setCards,
    setCurrentIndex,
    currentIndex,
  } = args;

  const handle = React.useCallback((e: React.SyntheticEvent<HTMLButtonElement>) => {
    const { name } = e.currentTarget;
    if (DEBUG) {
      if (cards.length <= currentIndex && currentIndex !== 0) {
        throw new Error(`CurrentIndex out of range. cards.length: ${ cards.length }, currentIndex: ${ currentIndex }`);
      }
    }
    if (cards.length > currentIndex) {
      setCards((prev) => update(
        prev,
        {
          [currentIndex]: {
            $merge: {
              lastView: new Date().getTime(),
              viewCount: prev[currentIndex].viewCount + 1,
              successCount: (name === 'yes' ? prev[currentIndex].successCount + 1 : prev[currentIndex].successCount),
            },
          },
        },
      ));
      setCurrentIndex((prev) => prev + 1);
    }
  }, [ cards, setCards, setCurrentIndex, currentIndex ]);

  return handle;
};
