import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import { Card as BPCard, ProgressBar } from '@blueprintjs/core';

import { Card } from '../db/CardSets';

const styles = StyleSheet.create({
  content: {
    textAlign: 'center',
    margin: '10px',
  },
  info: {
    marginTop: '10px',
  },
  text: {
    margin: '50px 0',
  },
});

export interface CardViewProps extends Card {}

export const CardView = (props: CardViewProps) => {
  const {
    sideA,
    sideB,
    viewCount,
    successCount,
  } = props;

  const [ frontSide, setFrontSide ] = React.useState(true);
  const flipCard = React.useCallback(() => {
    setFrontSide((prev) => !prev);
  }, [ setFrontSide ]);

  let content: string;
  if (frontSide) {
    content = sideA;
  } else {
    content = sideB;
  }

  const progress = viewCount > 0 ? successCount / viewCount : 0;

  return (
    <BPCard onClick={ flipCard } interactive>
      <div className={ css(styles.content) }>
        <h3 className={ css(styles.text) }>
          { content }
        </h3>
        <div className={ css(styles.info) }>
          <ProgressBar value={ progress } intent='success' animate={ false } stripes={ false } />
        </div>
      </div>
    </BPCard>
  );
};
