import lodash from 'lodash';

/** Merge arrB into arrA using `compare` function for equality checking. */
export const mergeArrays = (arrA: any[], arrB: any[], compare: (a: any, b: any) => boolean): any[] => {
  const arrBCopy = [ ...arrB ];
  const result: any = [];
  arrA.forEach((elementA) => {
    const index = arrB.findIndex((elementB) => compare(elementB, elementA));
    if (index !== -1) {
      result.push(arrB[index]);
      arrBCopy[index] = undefined;
    } else {
      result.push(elementA);
    }
  });

  return [ ...result, ...lodash.compact(arrBCopy) ];
};
