import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import { Navbar as BPNavbar, Button } from '@blueprintjs/core';

import { APP_NAME } from '../settings';

const styles = StyleSheet.create({
  navbarHeading: {
    '@media (max-width: 400px)': {
      display: 'none',
    },
  },
});

export interface NavbarProps {
  leftItems?: JSX.Element;
  rightItems?: JSX.Element;
  onHomeClick?: () => void;
}

export const Navbar = (props: NavbarProps) => {
  const {
    leftItems,
    rightItems,
    onHomeClick,
  } = props;

  return (
    <BPNavbar fixedToTop>
      <BPNavbar.Group align='left'>
        <BPNavbar.Heading
          className={ css(styles.navbarHeading) }
        >
          { APP_NAME }
        </BPNavbar.Heading>
        <Button onClick={ onHomeClick } icon='home' minimal />
        <BPNavbar.Divider className={ css(styles.navbarHeading) } />
        { leftItems }
      </BPNavbar.Group>
      <BPNavbar.Group align='right'>
        { rightItems }
      </BPNavbar.Group>
    </BPNavbar>
  );
};
