import * as React from 'react';
import lodash from 'lodash';
import { useParams } from 'react-router-dom';

import { arrayBufferToString } from '../lib';
import { AddDataDialog, AddDataDialogProps } from '../views/AddDataDialog';
import { createCard, Card } from '../db';
import { AppContext } from '../context/AppContext';

export interface AddCardsDialogProps extends Omit<AddDataDialogProps, 'onFileLoaded'> {}

export const AddCardsDialog = (props: AddCardsDialogProps) => {
  const { open, onClose } = props;
  const cardsetId = useParams().cardsetId;
  const ctx = React.useContext(AppContext);
  if (DEBUG) {
    if (!ctx) {
      throw new Error('Probably there is no AppContext.Provider uper in component tree.');
    }
  }

  const handleFileLoaded = React.useCallback((data: ArrayBuffer) => {
    const cards = createCardsFromArrayBuffer(data);
    ctx.onMergeCards(cardsetId, cards);
    onClose();
  }, [ ctx, cardsetId, onClose ]);

  return (
    <AddDataDialog open={ open } onClose={ onClose } onFileLoaded={ handleFileLoaded } />
  );
};

export const createCardsFromArrayBuffer = (data: ArrayBuffer): Card[] => {
  const str = arrayBufferToString(data);
  let newLineChar = '\n';
  if (str.indexOf('\r\n') !== -1) {
    newLineChar = '\r\n';
  }
  const lines = str.split(newLineChar);
  if (DEBUG) {
    const words = lines[0].split(',');
    if (words.length !== 4) {
      throw new Error(`Exepect 4 elements on the line but have ${ words.length }`);
    }
  }
  if (lines.length === 0 || lines[0].split(',').length !== 4) {
    return [];
  }

  return lodash.compact(lines.map((line) => {
    const words = line.split(',');
    // protect from blank lines or other kinds of noise in external file
    if (!words[2]) {
      return;
    }
    return createCard({ sideA: words[2], sideB: words[3] });
  }));
};
