import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import { Button } from '@blueprintjs/core';

import { CardThumbnail } from './CardThumbnail';
import { Card } from '../db';
import { Navbar } from './Navbar';
import { List } from './List';

const styles = StyleSheet.create({
  listItem: {
    margin: '10px 0',
  },
});

export interface CardListViewProps {
  onStartExercise: () => void;
  onCardDelete: (cardId: string) => void;
  onAddCards: () => void;
  cards: Card[];
  onHomeClick?: () => void;
}

export const CardListView = (props: CardListViewProps) => {
  const {
    onStartExercise,
    onCardDelete,
    cards,
    onAddCards,
    onHomeClick,
  } = props;

  const cardElements = cards.map((card) => (
    <CardThumbnail
      title={ card.sideA }
      cardId={ card.sideA }
      viewCount={ card.viewCount }
      successCount={ card.successCount }
      lastView={ card.lastView }
      onDelete={ onCardDelete }
      key={ card.sideA }
      className={ css(styles.listItem) }
    />
  ));

  const navbarLeftItems = <Button intent='primary' onClick={ onStartExercise }>start exercise</Button>;
  const navbarRightItems = <Button intent='primary' onClick={ onAddCards }>add cards</Button>;

  return (
    <>
      <Navbar leftItems={ navbarLeftItems } rightItems={ navbarRightItems } onHomeClick={ onHomeClick } />
      <List>
        { ...cardElements }
      </List>
    </>
  );
};
