export const routes = {
  main: '/',
  cardList: '/cardset/:cardsetId',
  exercise: '/cardset/:cardsetId/exercise',
};

export const getCardListLink = (cardsetId: string) => `/cardset/${ cardsetId }`;

export const getExerciseLink = (cardsetId: string) => `/cardset/${ cardsetId }/exercise`;

export interface RouterParams {
  cardsetId: string;
}
